import React from 'react';
import PanelButton from './PanelButton';


export default class Panel extends React.Component {
    constructor(props) {
        super(props);
        this.state = { open: false };
    }
    buttons() {
        return this.props.buttons ? this.props.buttons() : [];
    }
    footer() {
        return this.props.footer ? this.props.footer() : [];
    }
    open() {
        return this.state.open
    }
    stretch() {
        return this.props.stretch ? { height: '100%' } : {}
    }
    bodyBackgroundColor() {
        return this.props.bodyBackgroundColor ? { backgroundColor: this.props.bodyBackgroundColor } : {};
    }
    bodyStyle() {
        return {
            ...this.bodyBackgroundColor()
        }
    }
    render() {
        return (
            <div className="card direct-chat direct-chat-warning box-header" style={{ display: 'flex', paddingTop: 1, paddingBottom: 1, ...this.stretch() }}>
                <div className="card-header drag-me-header">
                    <h3 className="card-title">{this.props.title}</h3>
                    <div className="card-tools">
                        {/* <span title="3 New Messages" className="badge badge-warning">3</span> */}
                        {this.buttons()}
                        <PanelButton icon={this.open() ? 'fas fa-minus' : 'fas fa-plus'} onClick={() => {
                            this.setState({ open: !this.props.open });
                        }} />
                        <PanelButton icon={'fas fa-times'} />
                    </div>
                </div>
                <div className="card-body" style={{ ...this.bodyStyle() }}>
                    {this.props.children}
                </div>
                <div className="card-footer">
                    {this.footer()}
                </div>
            </div>
        )
    }
}