import React from 'react';
import styles from './MidiNotes.module.css';

export default class MidiNotes extends React.Component {
    render() {
        let notes = [];
        for (let i = 0; i < 128; i++) {
            if (this.props.notes && this.props.notes[i] && isOn(this.props.notes[i].command)) {
                notes.push({ midiId: i, value: true })
            }
            else {
                notes.push({ midiId: i, value: 0 })
            }
        }
        return (
            <div className={styles.notes}>
                {notes.map(note => {
                    return <div className={`${styles.whitekey} ${note.value ? styles.notekey : null}`} key={`midi-id-${note.midiId}`}>{note.midiId}</div>
                })}
            </div>
        );
    }
}
function parseMidiMessage(message) {
    return {
        command: message.data[0] >> 4,
        channel: message.data[0] & 0xf,
        note: message.data[1],
        velocity: message.data[2] / 127
    }
}
function isOn(message) {

    // Parse the MIDIMessageEvent.
    const { command, channel, note, velocity } = (message)

    // Stop command.
    // Negative velocity is an upward release rather than a downward press.
    if (command === 8) {
        if (channel === 0) return false;
        return true;
    }

    // Start command.
    else if (command === 9) {
        return true;
    }

    // Knob command.
    else if (command === 11) {
        if (note === 1) onModWheel(velocity)
    }

    // Pitch bend command.
    else if (command === 14) {
        onPitchBend(velocity)
    }
}
function onModWheel() { }
function onNote() { }
function onPad() { }
function onPitchBend() { }
/**
 * Handle a MIDI message from a MIDI input.
 */
function handleMidiMessage(message) {

    // Parse the MIDIMessageEvent.
    const { command, channel, note, velocity } = parseMidiMessage(message)

    // Stop command.
    // Negative velocity is an upward release rather than a downward press.
    if (command === 8) {
        if (channel === 0) onNote(note, -velocity)
        else if (channel === 9) onPad(note, -velocity)
    }

    // Start command.
    else if (command === 9) {
        if (channel === 0) onNote(note, velocity)
        else if (channel === 9) onPad(note, velocity)
    }

    // Knob command.
    else if (command === 11) {
        if (note === 1) onModWheel(velocity)
    }

    // Pitch bend command.
    else if (command === 14) {
        onPitchBend(velocity)
    }
}